#hambmenu {
  position: relative;
  width: 40px;
  height: 60px;
  float: left;
  -webkit-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  cursor: pointer;
  z-index: 2; }
  #hambmenu span {
    position: absolute;
    height: 4px;
    width: 80%;
    vertical-align: middle;
    margin: 20px 0 0 15px;
    background: white;
    border-radius: 8px;
    opacity: 1;
    left: 0;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    -webkit-transition: .25s ease-in-out;
    transition: .25s ease-in-out;
    padding: 0px; }

.hambclicker {
  content: '';
  height: 60px;
  width: 55px;
  position: absolute;
  top: 8px;
  left: 8px;
  cursor: pointer;
  z-index: 9; }

#hambmenu span:nth-child(1) {
  top: 0px; }

#hambmenu span:nth-child(2), #hambmenu span:nth-child(3) {
  top: 10px; }

#hambmenu span:nth-child(4) {
  top: 20px; }

#hambmenu.isopen span:nth-child(1) {
  top: 18px;
  width: 0%;
  left: 50%;
  opacity: 0; }

#hambmenu.isopen span:nth-child(2) {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg); }

#hambmenu.isopen span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg); }

#hambmenu.isopen span:nth-child(4) {
  top: 18px;
  width: 0%;
  left: 50%;
  opacity: 0; }

