/*Accordion*/
.accordion {
  margin: 0 auto; }

.accordion ul {
  list-style: none;
  margin: 0;
  padding: 0; }

.accordion li {
  margin: 0;
  padding: 25px; }

.accordion [type=radio], .accordion [type=checkbox] {
  display: none; }

.accordion label {
  display: block;
  font-size: 16px;
  line-height: 16px;
  background-color: #a9a9a9;
  color: black;
  font-weight: 700;
  cursor: pointer;
  text-transform: uppercase; }

.accordion ul li label:hover, .accordion [type=radio]:checked ~ label, .accordion [type=checkbox]:checked ~ label {
  color: white; }

.accordion .content {
  padding: 0 10px;
  overflow: hidden; }

.accordion .text {
  color: black;
  padding: 15px 10px; }

.accordion p:hover {
  color: red;
  cursor: pointer;
  background-color: #35e2df; }

.accordion h3 {
  padding: 0;
  margin: 10px 0; }

.vertical ul li {
  overflow: hidden;
  margin: 0 0 1px; }

.vertical ul li label {
  padding: 10px; }

.vertical [type=radio]:checked ~ label, .vertical [type=checkbox]:checked ~ label {
  border-bottom: 0; }

.vertical ul li label:hover {
  background-color: #d9d9d9; }

.vertical ul li .content {
  height: 0px;
  border-top: 0;
  -webkit-transition: all .5s ease-out;
  transition: all .5s ease-out; }

.vertical [type=checkbox]:checked ~ label ~ .content {
  height: 100%; }

